<template>
  <div>
    <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
      <!-- 电站组织树 -->
      <CommonTree
        title="电站组织"
        @select="handleSelect"
        :value="treeData"
        class="left-tree-layout"
        :toolbar="true"
        :canEdit="false"
        :canAdd="false"
        :canDelete="false"
        :isShowOperationBtns="false"
        :fieldNames="{ key: 'id', title: 'name' }"
      />

      <div class="right-table-layout">
        <div class="tab-button-div">
          <a-form
            :model="searchInfo"
            name="searchInfo"
            layout="inline"
            autocomplete="off"
            class="search-form ant-advanced-search-form"
          >
            <a-form-item
              name="billCode"
              label="单据编号"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, message: '请输入单据编号' }]"
              style="width: 26.5%"
              :colon="false"
            >
              <a-input
                v-model:value="searchInfo.billCode"
                placeholder="请输入单据编号"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item
              name="fileType"
              label="类型"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, message: '请选择类型' }]"
              style="width: 26.5%"
              :colon="false"
            >
              <DictSelectBox
                :type="`poSafetyFileType`"
                v-model:value="searchInfo.fileType"
                :width="'100%'"
              />
            </a-form-item>
            <a-form-item
              name="particularYear"
              label="年度"
              :labelCol="{ span: 6 }"
              :rules="[{ message: '请选择年度' }]"
              style="width: 26.5%"
              :colon="false"
            >
              <a-date-picker
                v-model:value="searchInfo.particularYear"
                value-format="YYYY"
                style="width: 100%"
                picker="year"
              />
            </a-form-item>
            <a-form-item v-if="expand" style="width: 100%; height: 10px" />
            <a-form-item
              v-if="expand"
              name="serviceCompany"
              label="服务单位"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, message: '请输入服务单位' }]"
              style="width: 26.5%"
              :colon="false"
            >
              <a-input
                v-model:value="searchInfo.serviceCompany"
                placeholder="请输入服务单位"
                style="width: 100%"
              />
            </a-form-item>
            <a-form-item
              v-if="expand"
              name="isRectificationCompleted"
              label="整改是否完成"
              :labelCol="{ span: 12 }"
              :rules="[{ required: false, message: '请输入整改是否完成' }]"
              style="width: 26.5%"
              :colon="false"
            >
              <DictSelectBox
                :type="`yes_or_no`"
                v-model:value="searchInfo.isRectificationCompleted"
                :width="'100%'"
              />
            </a-form-item>
            <a-form-item
              v-if="expand"
              name="status"
              label="审批状态"
              :labelCol="{ span: 6 }"
              :rules="[{ required: false, message: '请选择审批状态' }]"
              style="width: 26.5%"
              :colon="false"
            >
              <DictSelectBox
                :type="`bpm_process_instance_result`"
                @change="`()=> searchInfo.status = value`"
                v-model:value="searchInfo.status"
                :width="'100%'"
              />
            </a-form-item>
            <a-form-item class="placeholder-btns">
              <div style="width: 10px; opacity: 0"></div>
            </a-form-item>
            <a-form-item class="search-btns">
              <a-button style="margin: 0 8px" @click="resetFrom">重置</a-button>
              <a-button type="primary" class="tab-button" @click="searchList">查询</a-button>
              <a style="margin-left: 12px; font-size: 14px" @click="expandSearch">
                <template v-if="expand">
                  收起
                  <UpOutlined />
                </template>
                <template v-else>
                  展开
                  <DownOutlined />
                </template>
              </a>
            </a-form-item>
          </a-form>
        </div>
        <div class="tab-table-div">
          <BasicTable
            class="fix-basic-table"
            @register="registerTable"
            @resize-column="(w, col) => (col.width = w)"
            @row-db-click="(record) => handleView(record)"
          >
            <template #toolbar>
              <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreate">添加</a-button>
              <!-- <a-button type="primary" @click="handleUpdateIndex">修改</a-button> -->
              <a-button
                class="red-btn"
                :disabled="selectedRowId == ''"
                :preIcon="IconEnum.DELETE"
                @click="handleDelete"
                >删除</a-button
              >
              <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExportIndex"
                >导出</a-button
              >
            </template>

            <template #bodyCell="{ column, text, record }">
              <template v-if="column.key === 'action'">
                <TableAction
                  :actions="[
                    {
                      icon: IconEnum.LOG,
                      label: '进度',
                      onClick: handleView.bind(null, record),
                    },
                    {
                      icon: IconEnum.DELETE,
                      danger: true,
                      label: '取消',
                      ifShow: () => {
                        return record.status == 0;
                      },
                      onClick: handleCancel.bind(null, record),
                    },
                  ]"
                />
              </template>
            </template>
          </BasicTable>
        </div>
      </div>
      <!-- 数据表格 -->
    </PageWrapper>
  </div>
  <!-- <settlemenElecModal @reload="handleReload" @register="registerDrawer" /> -->
</template>
<script lang="ts" setup>
  import type { FormProps } from '/@/components/Form/src/types/form';
  import { onMounted, reactive, ref, toRaw } from 'vue';
  import { BasicTable, useTable, TableAction, SorterResult } from '/@/components/Table';
  import { TreeItem } from '/@/components/Tree';
  import { PageWrapper } from '/@/components/Page';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { message, SelectProps } from 'ant-design-vue';
  import { jsonToSheetXlsx } from '/@/components/Excel';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import {
    getSafetyFilesList,
    getTreeData,
    formState,
    getIndex,
    deleteSafetyFiles,
    getSafetyFilesListColumns,
    getDic,
    exportExcel,
  } from './safetyFiles';
  import { assign } from 'lodash-es';
  import { useRouter } from 'vue-router';
  import { useI18n } from '@/hooks/web/useI18n';
  import { useMessage } from '@/hooks/web/useMessage';
  import { ElMessageBox } from 'element-plus';
  import { cancelProcessInstance } from '@/api/bpm/processInstance';
  import { IconEnum } from '@/enums/appEnum';
  import { addTabPage } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import { DownOutlined, UpOutlined } from '@ant-design/icons-vue';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import * as CommonUtil from '/@/utils/common';

  // async function handleExportIndex() {
  //   createConfirm({
  //     title: t('common.exportTitle'),
  //     iconType: 'warning',
  //     content: t('common.exportMessage'),
  //     async onOk() {
  //       await exportExcel(getForm().getFieldsValue());
  //       createMessage.success(t('common.exportSuccessText'));
  //     },
  //   });
  // }
  import { useUserStore } from '/@/store/modules/user';

  const expand = ref(false);
  const { createConfirm, createMessage } = useMessage();
  const { t } = useI18n();
  const searchInfo = reactive<any>({
    fileType: null,
    particularYear: null,
    serviceCompany: null,
    isRectificationCompleted: null,
    status: null,
    billCode: null,
  }); // 查询信息
  const treeData = ref<TreeItem[]>([]); // 左侧电站树数据
  const selectedNode = ref({}); // 被选择的电站节点
  const periodParam = ref({}); // 查询分期下拉框数据的参数

  const checkedKeys = ref<Array<string | number>>([]); // 复选框的选择的数据
  const options = ref<SelectProps['options']>([]); //电站分期
  const areaColumn = getSafetyFilesListColumns(options);
  const router = useRouter();
  let partyDatasArr = ref<SelectProps['options']>([
    { label: '草稿', value: '0' },
    { label: '处理中', value: '1' },
    { label: '通过', value: '2' },
    { label: '不通过', value: '3' },
    { label: '已取消', value: '4' },
    { label: '已终止', value: '5' },
    { label: '退回', value: '6' },
  ]);
  const indexList = ref<Array<any>>([]);
  const getSafetyFilesListByParam = async () => {
    let areaList = await getSafetyFilesList(searchInfo);
    indexList.value = areaList.result;
    return areaList.result;
  };
  const searchForm: FormProps = {
    baseColProps: { lg: 6, md: 8 },
    labelWidth: 90,
    schemas: [],
  };

  function expandSearch() {
    redoHeight();
    expand.value = !expand.value;
  }

  // 注册Drawer
  // const [registerDrawer, { openDrawer: opensettlemenElecModal }] = useDrawer();

  const [registerTable, { reload, getPaginationRef, getForm, redoHeight }] = useTable({
    title: '安全信息列表',
    api: async () => {
      const paginationRef = getPaginationRef() as any;
      searchInfo.pageNo = paginationRef.current;
      searchInfo.pageSize = paginationRef.pageSize;
      let dataObj = await getSafetyFilesListByParam();
      return dataObj;
    },
    columns: areaColumn,
    useSearchForm: false,
    formConfig: searchForm,
    showTableSetting: true,
    bordered: true,
    // eslint-disable-next-line no-dupe-keys
    rowKey: 'id',
    // actionColumn: {
    //   width: 160,
    //   title: '操作',
    //   dataIndex: 'action',
    // },
    rowSelection: {
      type: 'radio',
      onChange: onSelect,
    },
    handleSearchInfoFn(info) {
      searchInfo.status = info.status;
      searchInfo.fileType = info.fileType;
      searchInfo.particularYear = info.particularYear;
      searchInfo.isRectificationCompleted = info.isRectificationCompleted;
      searchInfo.serviceCompany = info.serviceCompany;
      return info;
    },
    sortFn: (sortInfo: SorterResult) => {
      if (sortInfo.order) {
        searchInfo.sortField = sortInfo.field;
        searchInfo.sortOrder = sortInfo.order.replace('end', '');
      } else {
        searchInfo.sortField = null;
        searchInfo.sortOrder = null;
      }
    },
  });
  let selectedRowId = ref('');

  function onSelect(record, selected) {
    if (selected.length > 0) {
      checkedKeys.value = [record[0]];
      selectedRowId.value = record[0];
    } else {
      checkedKeys.value = [];
      selectedRowId.value = '';
    }
  }
  function onSelectAll(selected, selectedRows, changeRows) {
    const changeIds = changeRows.map((item) => item.id);
    if (selected) {
      checkedKeys.value = [...checkedKeys.value, ...changeIds];
    } else {
      checkedKeys.value = checkedKeys.value.filter((id) => {
        return !changeIds.includes(id);
      });
    }
  }

  // 查询左侧电站树数据
  async function queryDeptTreeList() {
    const deptList = (await getTreeData({})) as unknown as TreeItem[];
    treeData.value = deptList.result;
    // let org = deptList.result[0];
    // searchInfo.nodeKindId = org.nodeKindId;
    // searchInfo.searchId = org.id;
    reload();
  }
  // 添加区域
  function handleCreate() {
    formState.id = '';
    formState.fileType = '';
    formState.particularYear = '';
    formState.completionDate = '';
    formState.isRectificationCompleted = '';
    formState.serviceCompany = '';
    formState.isPowerGridFiling = '';
    formState.handledByName = '';
    formState.rectificationPlan = '';

    if (searchInfo.periodId == null) {
      message.error('请先选择电站分期。');
      return;
    }
    if (searchInfo.nodeKindId == 'station') {
      getOptions();
    }
    if (searchInfo.nodeKindId == 'period') {
      getOptions();
    }
    // opensettlemenElecModal(true);
    // router.push(`/po/safety/safetyfiles/create`);
    addTabPage(`/po/safety/safetyfiles/create`, '添加安全信息', toRaw(formState));
  }
  function handleView(record: any) {
    let str = '';
    if (record.status == 0) {
      str = '修改';
    } else if (record.status == 1) {
      str = '审批';
    } else {
      str = '查看';
    }
    CommonUtil.toFlowPage(record.id);
    // addTabPage(`/po/safety/safetyfiles/detail?id=${record.id}`, str + '安全信息');
  }

  const handleCancel = async (record: any) => {
    const res = await getIndex({ id: record.id });
    const { value } = await ElMessageBox.prompt('请输入取消原因', '取消流程', {
      confirmButtonText: t('common.okText'),
      cancelButtonText: t('common.cancelText'),
      inputPattern: /^[\s\S]*.*\S[\s\S]*$/,
      inputErrorMessage: '取消原因不能为空',
    });
    // 发起取消
    await cancelProcessInstance(res.result.processInstanceId, value);
    message.success('已取消');
    reload();
  };
  // 删除区域
  async function handleDelete() {
    console.log(checkedKeys.value);
    if (checkedKeys.value.length != 1) {
      message.info('请选择一条数据.');
      return false;
    }
    const res = await getIndex({ id: checkedKeys.value[0] });

    createConfirm({
      title: '提示',
      iconType: 'warning',
      content: '确定要删除选中记录？',
      async onOk() {
        if (res.result.status != 0) {
          message.error('非草稿状态不能删除。');
          return;
        }
        await ProcessInstanceApi.deleteProcessInstance(res.result.processInstanceId);
        message.success('已删除。');
        checkedKeys.value = [];
        reload();
      },
    });

    // deleteSafetyFiles(checkedKeys.value[0]).then(() => {
    //   message.success('已删除。');
    //   checkedKeys.value = [];
    //   reload();
    // });
  }
  function resetFrom() {
    searchInfo.fileType = null;
    searchInfo.particularYear = null;
    searchInfo.serviceCompany = null;
    searchInfo.isRectificationCompleted = null;
    searchInfo.status = null;
    searchInfo.billCode = null;
    reload();
  }

  // 查询
  function searchList() {
    reload();
  }

  // 根据电站id获取分期信息
  function getOptions() {
    let param = { stationId: periodParam.value.stationId };
    // let param = { stationId: '0E012356294F4AC2B81315E1F81BEBB9' };
  }
  // 左侧树状菜单选中事件
  function handleSelect(node = {}) {
    searchInfo.stationId = null;
    searchInfo.stationName = null;
    searchInfo.periodName = null;
    searchInfo.periodId = null;
    searchInfo.stationOrganId = null;
    searchInfo.stationOrganName = null;
    searchInfo.nodeKindId = null;
    periodParam.value.nodeKindId = null;
    searchInfo.searchId = null;
    if (node != null) {
      searchInfo.stationOrganId = node.organId;
      searchInfo.stationOrganName = node.organName;
      if (node.nodeKindId == 'station') {
        searchInfo.stationId = node.powerStationId;
        searchInfo.stationName = node.name;
        periodParam.value.stationId = node.powerStationId;
      }
      if (node.nodeKindId == 'period') {
        searchInfo.stationId = node.powerStationId;
        searchInfo.stationName = node.orgName;
        searchInfo.periodName = node.powerStationPeriodName;
        searchInfo.periodId = node.powerStationPeriodId;
        periodParam.value.stationId = node.powerStationId;

        formState.stationId = searchInfo.stationId;
        formState.stationName = searchInfo.stationName;
        formState.periodName = searchInfo.periodName;
        formState.periodId = searchInfo.periodId;
        formState.stationOrganId = searchInfo.stationOrganId;
        formState.stationOrganName = searchInfo.stationOrganName;
      }

      searchInfo.nodeKindId = node.nodeKindId;
      periodParam.value.nodeKindId = node.nodeKindId;
      searchInfo.searchId = node.id;
    }
    reload();
  }

  function checkSelectData() {
    if (checkedKeys.value.length == 0 || checkedKeys.value.length > 1 || !checkedKeys.value['0']) {
      message.error('请选择一行数据');
      return false;
    }
    return true;
  }
  // 导出
  async function handleExportIndex() {
    createConfirm({
      title: '导出',
      iconType: 'warning',
      content: '确定要导出所有记录？',
      async onOk() {
        const header = ref({ index: '序号' });
        const indexColumns = getSafetyFilesListColumns();
        const data = ref<Array<any>>([]);
        const keys = ref<Array<any>>([]);
        indexColumns.forEach((column) => {
          if (!(column.dataIndex === 'id')) {
            if (column.dataIndex === 'stationOrganName' && useUserStore().isMultiOrganization) {
              header.value[column.dataIndex] = column.title;
              keys.value.push(column.dataIndex);
            } else if (!(column.dataIndex === 'stationOrganName')) {
              header.value[column.dataIndex] = column.title;
              keys.value.push(column.dataIndex);
            }
          }
        });

        let areaList = await exportExcel(searchInfo);
        indexList.value = areaList.result;

        indexList.value.forEach(function (item, index) {
          let statusDisplayName = '';
          switch (item.status) {
            case 0:
              statusDisplayName = '草稿';
              break;
            case 1:
              statusDisplayName = '处理中';
              break;
            case 2:
              statusDisplayName = '通过';
              break;
            case 3:
              statusDisplayName = '不通过';
              break;
            case 4:
              statusDisplayName = '已取消';
              break;
            case 5:
              statusDisplayName = '已终止';
              break;
            case 6:
              statusDisplayName = '退回/驳回';
              break;
          }
          let exportItem = ref({ index: index + 1 });
          keys.value.forEach((key) => {
            exportItem.value[key] = key === 'status' ? statusDisplayName : item[key];
          });
          data.value.push(exportItem.value);
        });
        jsonToSheetXlsx({
          data: data.value,
          header: header.value,
          filename: '安全信息.xlsx',
        });
      },
    });
  }

  // 启动执行代码
  onMounted(() => {
    queryDeptTreeList();
    MsgManager.getInstance().listen('safetyFiles', function () {
      setTimeout(() => {
        reload();
      }, 500);
    });
  });
</script>
<style lang="less" scoped>
