    :deep(.ant-form-item-label) {
      width: 120px;
      max-width: 120px;
      text-align-last: right;
    }
  }

  .tab-table-div {
    margin-top: 10px;

    :deep(.ant-table-placeholder) {
      height: 591px;
    }
  }

  .tab-button {
    margin-right: 5px;

    &:last-child {
      margin-right: 0;
