    width: 100%;
    height: 100%;
    opacity: 0.3;
  }

  .search-form {
    position: relative;

    .search-btns {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }

  .left-tree-layout {
    flex-shrink: 0;
    width: 298px;
    border-radius: 2px;
  }

  .right-table-layout {
    width: calc(100% - 308px);
    padding: 10px;
  }

  .fix-basic-table {
    :deep(&.vben-basic-table .ant-table-wrapper) {
      padding: 10px 16px 12px;
    }

    :deep(.table-settings) {
      margin-right: 0;
